@import url(https://fonts.googleapis.com/css2?family=Oxygen:wght@400;700&display=swap);
.main {
    display: flex;
    flex-direction: column;
    margin: 15px;
    font-family: "Oxygen", sans-serif;
}

.favorite-button {
    background-color: rgb(177, 13, 122);
    border: none;
    padding: 10px 5px 5px 5px;
    color: white;
    font-weight: bold;
}
.main {
    width: 80%;
    margin: 0 auto;
}
.recipe {
    padding: 16px 13px 16px 14px;
    border-radius: 6px;
    border: solid 1px #141c3a;
    background-color: none;
    text-align: left;
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr;
  }

.recipes-container {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(160px, 1fr));
    grid-column-gap: 12px;
    grid-row-gap: 12px;
  }

  .recipe-container {
    grid-row-start: 1;
    grid-column-start: 1;
  }

  .image-container {
    width: 100%;
    height: 72px;
    overflow: hidden;
  }
  
  .recipe-image {
    width: 100%;
  }

  .AddToFavoriteButton {
    background-color: rgb(226, 75, 219);
    border: none;
    border-radius: 3px;
    padding: 5px;
    color: white;
  }
.searchBar {
    text-align: center;
    padding: 33px 36px 0 36px;
    position: relative;
}

.searchTerm {
    display: inline-block;
    width: 100%;
    text-indent: 8px;
    font-size: 16px;
    padding: 9px 25px 9px 40px;
    border-radius: 16px;
    border: solid 1px gray;
    margin-right: 10px;
    box-sizing: border-box;
}

.clearButton {
    position: absolute;
    right: 42px;
    top: 36px;
    bottom: 3px;
    border: 0;
    background: none;
    margin: 0;
    padding: 0 10px;
    border-radius: 100px;
    z-index: 2;
    cursor: pointer;
    display: flex;
    align-items: center;
}

.searchTerm::-webkit-input-placeholder {
    font-weight: bold;
    padding-left: 20px;
}

.searchTerm:-ms-input-placeholder {
    font-weight: bold;
    padding-left: 20px;
}

.searchTerm::placeholder {
    font-weight: bold;
    padding-left: 20px;
}

#search-icon {
    opacity: 0.5;
    position: absolute;
    top: 42px;
    left:53px;
}

.searchTerm:focus {
    outline: none;
}

.searchBar span {
    position: absolute;
    right: 1em;
    top: 0;
    bottom: 0;
    line-height: 40px;
    text-align: center;
}
